.App-header {
  display: flex;
  flex-direction: column;
  flex: 0 1 auto;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-body {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  justify-content: space-between;
  align-items: center;
  color: white;
  flex-grow: 1;
  width: 100%;
  height: 100%;
  gap: 5px;
}

.App-link {
  color: #61dafb;
}

